.product-section-container{
    .product-section-title{
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        font-weight: 400;
        font-size: 28px;
        margin-bottom: 20px;
    }
    .product-section-btns{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 10px 0;
        .sub-categories-btn{
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
        }
    }
    .product-cards-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}